.form {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;

  &__label {
    display: flex;
    flex-direction: column-reverse;
  }

  .just-validate-error-label {
    color: var(--color-light) !important;
    margin-bottom: 3px;
  }

  &__input {
    padding: 20px;
    width: 100%;
    border-radius: 5px;
    font-size: 18px;
    color: var(--color-dark);

    &::placeholder {
      color: rgba(40, 41, 68, 0.70);
    }
  }
}
