.header {
  padding-top: 30px;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include high-tablet {
      justify-content: initial;
    }
  }

    &__phone {
      @include high-tablet {
        position: relative;
        margin-left: auto;
        margin-right: 10px;
        border: 1px solid var(--color-light);
        border-radius: 10px;
        width: 44px;
        height: 44px;
       font-size: 0;
       background-image: url('../img/phone.svg');
       background-position: center;
       background-size: 24px 24px;
       background-repeat: no-repeat;
      }
    }}
