.graph-modal__close {
  right: 40px;
  top: 40px;
  width: auto;
  height: auto;
  color: var(--color-text);
  font-size: 18px;
  font-weight: 400;
  line-height: 160%;
  background-image: initial;

  @include mobile {
    right: 20px;
    top: 20px;
  }
}

.graph-modal {
  &::before {
    @include tablet {
      display: none;
    }
  }
}

.popup {
  padding: 40px;
  width: 100%;
  max-width: 940px;

  &__top {
    margin-bottom: 40px;
    display: flex;
    gap: 40px;

    @include mobile {
  gap: 20px;
    }
  }

  &__image {}

  &__text {}

  &__name {
    margin-bottom: 10px;
    color: var(--color-dark);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%;
  }

  &__post {
    margin-bottom: 10px;
    color: var(--color-text);
    font-size: 18px;
    line-height: 160%;
  }

  &__list {
    display: flex;
    gap: 10px;
  }

  &__item {}

  &__link {
    display: block;
    width: 30px;
    height: 30px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      fill: var(--color-dark);
    }
  }

  &__bottom-content {
    max-height: 375px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__info {
    color: var(--color-dark);
    font-size: 20px;
    line-height: 160%;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    color: var(--color-dark);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%;
  }

  .simplebar-track.simplebar-vertical {
    width: 5px;
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
  }

  .simplebar-scrollbar::before {
    left: 0;
    right: 0;
    width: 5px;
    border-radius: 40px;
    background: var(--color-dark);
    opacity: 1;
  }

  @include small-tablet {
    padding-top: 80px;
  }

  @include mobile {
    padding: 15px;
    padding-top: 40px;
  }

}

.tabs {

  &__menu {
    margin-bottom: 20px;
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
  }

  &__nav {
    padding-bottom: 20px;
    max-width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include small-tablet {
      max-width: 100%;
    }


    @include mobile {
      flex-wrap: wrap;
      gap: 10px;
    }
  }

  &__nav-item {}

  &__nav-btn {
    color: var(--color-dark);
    font-size: 20px;
    font-weight: 400;
    line-height: 160%;

    &:focus {
      color: var(--color-accent);
      background-color: transparent;
    }

    &--active {
      outline: none;
      color: var(--color-accent);
      background-color: transparent;
    }
  }

  &__content {}
}
