.benefits {
  padding-top: 100px;
  padding-bottom: 100px;
  background: var(--color-bg);

  @include mobile {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &__title {
    margin-bottom: 60px;

    @include mobile {
     margin-bottom: 20px;
    }
  }

  &__list {
    @include mobile {
grid-template-columns: repeat(4, 1fr);
gap: 20px;
    }
  }

  &__item {
    grid-column: 4 span;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
          }
  }

  &__subtitle {
    margin-bottom: 20px;
  }
}
