.nav {
display: flex;
@include high-tablet {
  justify-content: center;
}

  &__list {
    display: flex;
    align-items: center;
    gap: 40px;

    @include high-tablet {
      flex-direction: column;
      justify-content: center;
      gap: 20px;
      height: 100%;
    }
  }

  &__link {
    font-size: 20px;
    line-height: 160%;
    color: var(--color-light);

    @include high-tablet {
      font-size: 30px;
      font-weight: 600;
    }
  }

  @include high-tablet {
    position: fixed;
    z-index: 500;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: var(--color-dark);
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
