.footer {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: var(--color-dark);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__lefft {
    @include mobile {
   margin-bottom: 20px;
    }
  }

  &__logo {
    display: inline-block;
    margin-bottom: 5px;
  }

  &__copy {
    color: rgba(255, 255, 255, 0.70);
    line-height: 160%;
  }
}
