.trainers {
  padding-top: 120px;
  padding-bottom: 60px;

  &__title {
    margin-bottom: 60px;
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--container-width)) / 2));
    margin-bottom: 60px;
  }

  &__image {
    margin-bottom: 20px;
    max-height: 500px;
    border-radius: 20px;
  }

  &__name {
    margin-bottom: 10px;
  }

  &__post {
    margin-bottom: 20px;

    @include mobile {
      min-height: 50px;
    }
  }

  &__btn {
    color: var(--color-accent);
    font-size: 18px;
    line-height: 160%;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__scroll {
    border-radius: 40px;
    width: 360px;
    height: 5px;
    background: rgba(20, 16, 36, 0.08);
  }

  .swiper-scrollbar-drag {
    background: var(--color-dark);
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 10px;

    @include mobile {
    display: none;
    }
  }

  &__slider-btn {
    width: 40px;
    height: 40px;
    transition: fill .3s ease-in-out;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include hover {
      fill: var(--color-accent);
    }

  }

  .swiper-slide {
    width: 360px;

    @include mobile {
      width: 220px;
    }
  }

}
