.about {
  padding-top: 120px;
  padding-bottom: 120px;

  &__descr {
    margin-bottom: 60px;
    max-width: 891px;
    font-size: 30px;
    line-height: 160%;
    color: var(--color-dark);

    span {
      font-weight: 600;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 24px;
    }
  }

  &__list {
    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__digit {
    color: var(--color-accent);
    font-size: 70px;
    font-weight: 600;
    line-height: 160%;
  }


  @include mobile {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
