.title {
  color: var(--color-dark);
  font-size: 50px;
  font-weight: 600;
  line-height: 160%;

  @include mobile {
    text-align: center;
    font-size: 30px;
    line-height: 140%;
  }
}

.subtitle {
  color: var(--color-dark);
  font-size: 24px;
  font-weight: 600;
  line-height: 160%;

  @include mobile {
    text-align: center;
    font-size: 20px;
  }
}
