.burger {
  @include high-tablet {
    position: relative;
    border: 1px solid var(--color-light);
    border-radius: 10px;
    width: 44px;
    height: 44px;

    &__line {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 26px;
      height: 2px;
      background-color: var(--color-light);
      transform: translate(-50%, -50%);

      &:first-child {
        margin-top: -7px;
      }

      &:last-child {
        left: 9px;
        margin-top: 7px;
        width: 13px;
        transform: translateY(-50%);
      }
    }
  }

}

.burger-close {
  @include high-tablet {
    position: absolute;
    top: 30px;
    right: 15px;
    border: 1px solid var(--color-light);
    border-radius: 10px;
    width: 44px;
    height: 44px;
    background-image: url('../img/close.svg');
    background-position: center;
    background-size: 14px 12px;
    background-repeat: no-repeat;
  }
}
