.tooltip {
margin-left: 10px;

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border-radius: 100px;
    background: rgba(20, 16, 36, 0.08);


    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    bottom: 11px !important;
    padding: 15px;
    border-radius: 5px;
    background: var(--color-light);
    box-shadow: 0px 2px 8px 0px rgba(20, 16, 36, 0.10);
    color: var(--color-dark);
    text-align: center;
    font-family: Jost;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
}
