.tariffs {
  padding-top: 60px;
  padding-bottom: 120px;

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    @include high-tablet {
      gap: 20px;
    }

    @include mobile {
      display: block;
    }
  }

  &__item {
    grid-column: 4 span;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
}
