.btn {
  padding: 25px;
  text-align: center;
  font-size: 18px;
  line-height: 160%;
  color: var(--color-light);
  border-radius: 5px;
  background: var(--color-accent);

  &--dark {
    padding: 19px;
    color: var(--color-light);
text-align: center;
font-size: 18px;
line-height: normal;
border-radius: 5px;
background: var(--color-dark);

  }
}
