.hero {
  padding-top: 160px;
  min-height: 800px;
  background-color: var(--color-dark);

  &__container {
    display: flex;
  }

  &__content {
    max-width: 516px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include high-tablet {
      max-width: none;
      align-items: flex-start;
    }

    @include mobile {
      align-items: center;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 70px;
    font-weight: 600;
    line-height: 160%;
    color: var(--color-light);

    @include mobile {
      margin-bottom: 10px;
      text-align: center;
      font-size: 30px;
      font-style: normal;
    }
  }

  &__descr {
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 160%;
    color: var(--color-light);

    @include mobile {
      text-align: center;
      font-size: 16px;
      font-weight: 400;
      line-height: 160%;
    }
  }

  &__link {
    display: inline-block;
    min-width: 260px;
  }

  &__image {
    transform: translateX(50px);

    @include high-tablet {
      display: none;
    }
  }

  @include high-tablet {
    padding-bottom: 100px;
   min-height: auto;
  }
}
