.card {
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  border: 1px solid rgba(20, 16, 36, 0.20);

  &--current {
    background: var(--color-dark);
    border: 1px solid var(--color-dark);
    transform: scale(1.1);

    .card__title {
      color: var(--color-light);
    }

    .card__price {
      color: var(--color-light);
    }

    .card__descr {
      color: var(--color-light);
    }

    @include high-desktop {
      transform: scale(1);
    }
  }

  &__title {
    margin-bottom: 83px;
    color: var(--color-dark);
    font-size: 30px;
    font-weight: 600;
    line-height: 160%;
  }

  &__price {
    margin-bottom: 20px;
    color: var(--color-dark);
    font-size: 50px;
    font-weight: 600;
    line-height: 160%;

    @include high-desktop {
      font-size: 42px;
    }
  }

  &__descr {
    margin-bottom: auto;
    color: var(--color-dark);
    text-align: center;
    font-size: 18px;
    line-height: 160%
  }

  &__btn {
    width: 100%;
    margin-top: 52px;
  }


  @include high-tablet {
    padding: 15px;
  }

}
