.order {
  padding-top: 87px;
  padding-bottom: 87px;
  background-color: var(--color-accent);

  &__container {
    @include small-tablet {
      display: block;
    }
  }

  &__left {
    grid-column: 5 span;
    padding-left: 60px;

    @include small-tablet {
      padding-left: 0;
     margin-bottom: 20px;
    }
  }

  &__right {
    padding-right: 60px;
    grid-column: 7 span;
    display: flex;
    justify-content: flex-end;
    gap: 10px;

    @include small-tablet {
      padding-right: 0;
      justify-content: center;
    }
  }

  &__title {
    margin-bottom: 20px;
    color: var(--color-light);
    font-size: 36px;
    font-weight: 600;
    line-height: 130%;
  }

  &__descr {
    color: var(--color-light);
    font-size: 20px;
    line-height: 130%;
  }
}

